
import { defineComponent } from "vue";
import { modalController } from "@ionic/vue";
import BreadCrumb from "@/shared/components/BreadCrumb/BreadCrumb.vue";
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue'
import ModifyCard from '@/shared/components/ModifyCard/ModifyCard.vue'
import { APM20300121_REQ, APM20300121_RES } from "@/shared/type/APM20300121.type";
import { APM20300112_REQ, APM20300112_RES, IB_AUTHORIZATION_DETAIL } from "@/shared/type/APM20300112.type";
import { APM20300131_REQ, APM20300131_RES } from "@/shared/type/APM20300131.type";
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";

export default defineComponent({
  name: "apm203001sc01",
  components: {
    BreadCrumb,
    SelectOption,
    ModifyCard
  },

  data() {
    const authCodeDetail = {
      authorizationBizTypeCode1: "",
      authorizationBizTypeCode2: "",
      businessCommonStatusCode: "01",
      authorizationAuthorityTypeCode: "",
      authorizationBizTypeCode1Name: "",
      managerApprovalDescription: "",
      enableApprovalLineYN: "Y",
      financialTransactionYN: "Y",
    } as IB_AUTHORIZATION_DETAIL;

    const viewType = this.$route.query.type;

    const modifyCard = {
      registerTellerId: "",
      registerTellerName: "",
      registerDate: "",
      registerTime: "",
      updatedTellerId: "",
      updatedTellerName: "",
      updatedDate: "",
      updatedTime: "",
      deleteTellerId: "",
      deleteTellerName: "",
      deleteDate: "",
      deleteTime: "",
    };

    return {
      authCodeDetail,
      modifyCard,
      isDisableField: true,
      viewType,
      isInvalid: false,
      showDefault: false,
      showDisabled: false,
      disableKey: false,
      approvalRes: {} as any,
      operationMode: 'normal' as screenMode,
      enableApprovalStatus: [
        { key: 'Y', value: 'Enable' },
        { key: 'N', value: 'Disable' }
      ],
      financialTransactionStatus: [
        { key: 'Y', value: 'Financial' },
        { key: 'N', value: 'None Financial ' }
      ]
    };
  },

  async mounted() {
    if (this.approvalType === '') {
      if (this.viewType === "edit") {
        this.getAuthorizationCodeDetail();
        this.inputUpdate();
      } else {
        this.inputRegister();
      }
    } else {

      await this.$approval.getAuthorizationDetail().then((authDetail: APM20200113_RES) => {
        let authDetailParsed = JSON.parse(authDetail.body.requestContent)

        this.approvalRes = authDetail.body

        this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'

        this.authCodeDetail = {
          authorizationBizTypeCode1: authDetailParsed.authorizationBizTypeCode1,
          authorizationBizTypeCode2: authDetailParsed.authorizationBizTypeCode2,
          authorizationBizTypeCode1Name: authDetailParsed.authorizationBizTypeCode1Name,
          authorizationAuthorityTypeCode: authDetailParsed.authorizationAuthorityTypeCode,
          managerApprovalDescription: authDetailParsed.managerApprovalDescription,
          businessCommonStatusCode: authDetailParsed.businessCommonStatusCode,
          enableApprovalLineYN: authDetailParsed.enableApprovalLineYN,
          financialTransactionYN: authDetailParsed.financialTransactionYN
        };
      })
    }
  },

  computed: {
    queryRoute() {
      return this.$route.query;
    },
    approvalType(): string {
      return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
    },
  },

  methods: {
    inputRegister() {
      this.showDefault = true;
      this.showDisabled = false;
      this.disableKey = true;
    },

    inputUpdate() {
      this.showDefault = false;
      this.showDisabled = true;
      this.disableKey = true;
    },

    preventLeadingSpace(e: Event) {
      if (!(e.target as HTMLInputElement).value) e.preventDefault();
      else if (
        (e.target as HTMLInputElement).value[0] ||
        (e.target as HTMLInputElement).value[1] ||
        (e.target as HTMLInputElement).value[2] === " "
      )
        (e.target as HTMLInputElement).value = (e.target as HTMLInputElement).value.replace(/\s/g, "");
    },

    async getAuthorizationCodeDetail() {
      const body = {
        authorizationBizTypeCode1: this.queryRoute.authorizationBizTypeCode1,
        authorizationBizTypeCode2: this.queryRoute.authorizationBizTypeCode2,
        authorizationAuthorityTypeCode: this.queryRoute.authorizationAuthorityTypeCode,
      } as APM20300112_REQ;

      const res = (await this.$request.getRequest("APM20300112", body, false)) as APM20300112_RES;

      this.modifyCard = {
        registerTellerId: String(res.body.firstRegisterTellerID),
        registerTellerName: String(res.body.firstRegisterTellerName),
        registerDate: String(res.body.firstRegisterDate),
        registerTime: String(res.body.firstRegisterTime),
        updatedTellerId: String(res.body.lastChangeTellerID),
        updatedTellerName: String(res.body.lastChangeTellerID),
        updatedDate: String(res.body.lastChangeDate),
        updatedTime: String(res.body.lastChangeTime),
        deleteTellerId: '',
        deleteTellerName: '',
        deleteDate: '',
        deleteTime: '',
      };

      this.authCodeDetail = {
        authorizationBizTypeCode1: res.body.authorizationBizTypeCode1,
        authorizationBizTypeCode2: res.body.authorizationBizTypeCode2,
        authorizationBizTypeCode1Name: res.body.authorizationBizTypeCode1Name,
        authorizationAuthorityTypeCode: res.body.authorizationAuthorityTypeCode,
        managerApprovalDescription: res.body.managerApprovalDescription,
        businessCommonStatusCode: res.body.businessCommonStatusCode,
        enableApprovalLineYN: res.body.enableApprovalLineYN,
        financialTransactionYN: res.body.financialTransactionYN,
      };
    },

    async registerAuthorizationCode() {
      const body: APM20300121_REQ = {
        authorizationBizTypeCode1: this.authCodeDetail.authorizationBizTypeCode1,
        authorizationBizTypeCode2: this.authCodeDetail.authorizationBizTypeCode2,
        businessCommonStatusCode: this.authCodeDetail.businessCommonStatusCode,
        authorizationAuthorityTypeCode: this.authCodeDetail.authorizationBizTypeCode2,
        managerApprovalDescription: this.authCodeDetail.managerApprovalDescription,
        enableApprovalLineYN: this.authCodeDetail.enableApprovalLineYN,
        financialTransactionYN: this.authCodeDetail.financialTransactionYN,
      };

      const res = (await this.$request.getRequest("APM20300121", body, false)) as APM20300121_RES;

      if (res.header.result) {
        this.$dialog.onOpen({
          content: this.$t("message.register_success"),
          dialogMode: "Success",
          callback: () => {
            modalController.dismiss();
          },
        });
      }

      return res.header.result;
    },

    async updateAuthorizationCode() {
      const body: APM20300131_REQ = {
        authorizationBizTypeCode1: this.authCodeDetail.authorizationBizTypeCode1,
        authorizationBizTypeCode2: this.authCodeDetail.authorizationBizTypeCode2,
        businessCommonStatusCode: this.authCodeDetail.businessCommonStatusCode,
        authorizationAuthorityTypeCode: this.authCodeDetail.authorizationAuthorityTypeCode,
        managerApprovalDescription: this.authCodeDetail.managerApprovalDescription,
        enableApprovalLineYN: this.authCodeDetail.enableApprovalLineYN,
        financialTransactionYN: this.authCodeDetail.financialTransactionYN,
      };

      const res = (await this.$request.getRequest("APM20300131", body, false)) as APM20300131_RES;

      if (res.header.result) {
        this.$dialog.onOpen({
          content: this.$t("message.update_success"),
          dialogMode: "Success",
          callback: () => {
            this.getAuthorizationCodeDetail();
            this.inputUpdate();
            modalController.dismiss();
          },
        });
      }

      return res.header.result;
    },

    handleSave() {
      this.isInvalid = false;
      if (
        this.authCodeDetail.authorizationBizTypeCode1 === "" ||
        this.authCodeDetail.businessCommonStatusCode === "" ||
        this.authCodeDetail.authorizationBizTypeCode2 === "" ||
        this.authCodeDetail.managerApprovalDescription === ""
      ) {
        this.isInvalid = true;
        return;
      }

      this.$dialog.onOpen({
        content: `${this.$t("message.would_you_like_to_save")}`,
        dialogMode: "Question",
        showCancel: true,
        callback: () => {
          if (this.viewType === "edit") {
            this.updateAuthorizationCode().then((res: boolean) => {
              if (res) {
                this.getAuthorizationCodeDetail();
                this.inputUpdate();
                modalController.dismiss();
              }
            });
          } else {
            this.registerAuthorizationCode().then((res: boolean) => {
              if (res) {
                this.goToList();
                modalController.dismiss();
              }
            });
          }
          modalController.dismiss();
        },
      });
    },

    handleCancel() {
      this.$dialog.onOpen({
        content: `${this.$t("message.cancel_without_saving_changes")}`,
        dialogMode: "Question",
        callback: () => {
          if (this.viewType === "edit") {
            this.inputUpdate();
            this.getAuthorizationCodeDetail();
          } else {
            this.goToList();
          }
          modalController.dismiss();
        },
      });
    },

    goToList() {
      this.$router.push("/APM203001SC00");
    },

    handleEdit() {
      this.inputRegister();
      this.disableKey = false;
    },

    dialogValidate(title: string, content: string) {
      this.$dialog.onOpen({
        title: title,
        content: content,
        btnLabel: `${this.$t("common.okay")}`,
        showCancel: false,
        callback: () => modalController.dismiss(),
      });
    },

    dialogMessage(message: string) {
      this.$dialog.onOpen({
        title: `${this.$t("message.alert")}`,
        content: message,
        showCancel: false,
        callback: () => {
          modalController.dismiss();
        },
      });
    },
  },
});
